
.game-list {
  background: #f4f4f4;
}
.game-list .list-item {
    display: inline-block;
    box-sizing: border-box;
    width: 2.5rem;
    height: 2.5rem;
    border-right: 1px dashed #ddd;
    border-bottom: 1px dashed #ddd;
    text-align: center;
    font-size: 12px;
}
.game-list .list-item img {
      width: 1.25rem;
      height: 1.25rem;
      margin-top: 0.625rem;
      margin-bottom: 0.20833rem;
      border-radius: 50%;
}



